import React, { useEffect, useMemo } from 'react';
import PageWrapper from '../components/PageWrapper';
import { useAuthEffects } from '../components/Auth';
import { useTheme, Box, Grid2, Tab, Tabs, useMediaQuery, Button} from '@mui/material';
import ChatBox from '../components/Chatbox';
import Stepper from '../components/Stepper';
import CloseIcon from '@mui/icons-material/Close';
import ShoppingCartCheckoutIcon from '@mui/icons-material/ShoppingCartCheckout';
import { useNavigate, useParams } from 'react-router-dom';
import { useOrderDeleteMutation, useOrderDetailsLoadMutation } from '../services/orders';
import { useAppSelector } from '../hooks';
import { selectOrderDetails, selectOrderMessages } from './orderSlice';
import OrderViewResourceList from '../components/OrderViewResourceList';

function a11yProps(index: number) {
	return {
		id: `simple-tab-${index}`,
		'aria-controls': `simple-tabpanel-${index}`,
	};
}

interface TabPanelProps {
	children?: React.ReactNode;
	index: number;
	value: number;
}

function CustomTabPanel(props: TabPanelProps) {
	const { children, value, index, ...other } = props;
	return (
		<Box
			flex={1}
			overflow='auto'
			role="tabpanel"
			hidden={value !== index}
			id={`simple-tabpanel-${index}`}
			aria-labelledby={`simple-tab-${index}`}
			{...other}
		>
			{children}
		</Box>
	);
}

const OrderDetailView = () => {
	useAuthEffects();

	const {id} = useParams();
	const theme = useTheme();
	const isSmUp = useMediaQuery(theme.breakpoints.up('sm'));
	const [orderDetailsLoadMutation] = useOrderDetailsLoadMutation();
	const [orderDeleteMutation, { isSuccess: isDeleteSuccess }] = useOrderDeleteMutation();
	const [tabValue, setTabValue] = React.useState(0);
	const orderDetails = useAppSelector(selectOrderDetails);
	const messages = useAppSelector(selectOrderMessages);
	const navigate = useNavigate();

	useEffect(() => {
		if (id) {
			orderDetailsLoadMutation(id);
		}
	}, [id]);

	const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
		setTabValue(newValue);
	};

	const handleCancelOrderClick = () => {
		if (id) {
			orderDeleteMutation(id);
		}
	};

	useEffect(() => {
		if (isDeleteSuccess) {
			navigate('/orders');
		}
	}, [isDeleteSuccess]);

	const order = useMemo(() => {
		return orderDetails ? orderDetails.main : null;
	}, [orderDetails]);

	const materials = useMemo(() => {
		return orderDetails ? orderDetails.materials : [];
	}, [orderDetails]);

	return <PageWrapper noScroll={tabValue === 1} disableFooter={tabValue === 1}>
		<Box sx={{mt: 4, mb: 2}}>
			{order && <Stepper 
				orderStatus={order.status}
				orderDate={order.createdAt}
			/>}
		</Box>
		<Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
			<Tabs value={tabValue} onChange={handleTabChange} aria-label="basic tabs example">
				<Tab label="Items" {...a11yProps(0)} />
				<Tab label="Messages" {...a11yProps(1)} />
			</Tabs>
		</Box>
		<CustomTabPanel value={tabValue} index={0}>
			<OrderViewResourceList items={materials} />
			<Grid2 container spacing={2}>
				<Grid2 size={isSmUp ? 'grow' : 12} m={1} mb={3}>
					You&apos;ll be notified when the offer has been prepared!
				</Grid2>
				<Grid2 size={isSmUp ? 'auto' : 12}>
					<Button onClick={() => null} startIcon={<ShoppingCartCheckoutIcon />} disabled={true}>Proceed to checkout</Button>
				</Grid2>
				<Grid2 size={isSmUp ? 'auto' : 12}>
					<Button onClick={() => handleCancelOrderClick()} startIcon={<CloseIcon />} color='error'>Cancel order</Button>
				</Grid2>
			</Grid2>
		</CustomTabPanel>
		<CustomTabPanel value={tabValue} index={1}>
			{order && <ChatBox orderId={order.id} messages={messages} isOpen={tabValue===1} />}
		</CustomTabPanel>
	</PageWrapper>;
};

export default OrderDetailView;
