import React, { useEffect } from 'react';
import PageWrapper from '../components/PageWrapper';
import { useAuthEffects } from '../components/Auth';
import { Divider, Grid2, ImageListItem, ImageList, useMediaQuery, ImageListItemBar, useTheme, IconButton } from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import { useNavigate } from 'react-router-dom';
import { useDiscoverLoadMutation } from '../services/plans';
import { useAppSelector } from '../hooks';
import { selectPlanListItems } from './planSlice';
import dayjs from 'dayjs';
import relativeTime from 'dayjs/plugin/relativeTime';

dayjs.extend(relativeTime);

const DiscoverView = () => {
	useAuthEffects();

	const theme = useTheme();
	const isSmUp = useMediaQuery(theme.breakpoints.up('sm'));
	const navigate = useNavigate();
	const [discoverLoadMutation] = useDiscoverLoadMutation();
	const plans = useAppSelector(selectPlanListItems);

	useEffect(() => {
		discoverLoadMutation();
	}, []);

	const onAddButtonClick = (e: React.MouseEvent<HTMLButtonElement>, planId: number) => {
		e.stopPropagation();
	};

	return <PageWrapper>
		<Grid2 container spacing={8}>
			<Grid2 size={12}>
				<Divider sx={{mt: 1}}/>
				<ImageList cols={isSmUp ? 3 : 1} gap={8} rowHeight={400}>
					{plans.map((item) => {
						const timeTillNow = dayjs(item.publishedAt).fromNow();
						return <ImageListItem key={item.imgUrl} onClick={() => navigate(`/plans/${item.id}`)} sx={{cursor: 'pointer', overflowX:'hidden'}}>
							{/* <Box sx={{borderRadius: 2}}> */}
							<img
								srcSet={`${item.imgUrl}?w=248&fit=crop&auto=format&dpr=2 2x`}
								src={`${item.imgUrl}?w=248&fit=crop&auto=format`}
								alt={item.title}
								loading="lazy"
								style={{borderRadius: '8px', boxShadow: theme.shadows[2], overflow: 'hidden'}}
							/>
							{/* </Box> */}
							<ImageListItemBar
								title={item.title}
								subtitle={<><span>by: @{item.authorName}</span><span> &middot; {timeTillNow}</span></>}
								actionIcon={
									<IconButton
										sx={{ color: 'rgba(255, 255, 255, 0.54)', mt:1}}
										aria-label={`info about ${item.title}`}
										onClick={(e) => onAddButtonClick(e, item.id)}
									>
										<AddIcon />
									</IconButton>
								}
								position='below'
							/>
						</ImageListItem>;
					})}
				</ImageList>
			</Grid2>
		</Grid2>
	</PageWrapper>;
};

export default DiscoverView;
