import React, { ReactElement, ReactNode } from 'react';
import PageHeader from './PageHeader';
import PageFooter from './PageFooter';
import { Box, Container } from '@mui/material';

interface Props {
    children: ReactNode;
	disableFooter?: boolean;
	noScroll?: boolean;
}

const PageWrapper = ({children, disableFooter, noScroll}: Props) => {
	return <>
		<Container>
			<Box sx={noScroll ? {display:'flex', flexDirection: 'column', height: '100vh', overflow: 'hidden'} : {}}>
				<PageHeader />
				{children}
				{!disableFooter && <PageFooter />}
			</Box>
		</Container>
	</>;
};

export default PageWrapper;