import React from 'react';
import PageWrapper from '../components/PageWrapper';
import { useAuthEffects } from '../components/Auth';
import { Divider, Grid2, ImageListItem, ImageList, useMediaQuery, ImageListItemBar, useTheme, IconButton } from '@mui/material';
import RemoveIcon from '@mui/icons-material/Remove';

const DiscoverView = () => {
	useAuthEffects();

	const theme = useTheme();
	const isSmUp = useMediaQuery(theme.breakpoints.up('sm'));

	return <PageWrapper>
		<Grid2 container spacing={8}>
			<Grid2 size={12}>
				<Divider sx={{mt: 1}}/>
				<ImageList cols={isSmUp ? 3 : 1} gap={8} rowHeight={400}>
					{itemDataNew.map((item) => {
						const hoursInfo = <span>&middot; {Math.round(Math.random() * 10)} hours ago</span>;
						return <ImageListItem key={item.img}>
							<img
								srcSet={`${item.img}?w=248&fit=crop&auto=format&dpr=2 2x`}
								src={`${item.img}?w=248&fit=crop&auto=format`}
								alt={item.title}
								loading="lazy"
								style={{overflow:'hidden'}}
							/>
							<ImageListItemBar
								title={item.title}
								subtitle={<><span>by: {item.author}</span>{hoursInfo}</>}
								actionIcon={
									<IconButton
										sx={{ color: 'rgba(255, 255, 255, 0.54)' }}
										aria-label={`info about ${item.title}`}
									>
										<RemoveIcon />
									</IconButton>
								}
								position="below"
							/>
						</ImageListItem>;
					})}
				</ImageList>
			</Grid2>
		</Grid2>
	</PageWrapper>;
};

export default DiscoverView;

const itemDataNew = [
	{
		'img': 'https://images.unsplash.com/photo-1584225064281-429e7fce4961?w=800&auto=format&fit=crop&q=60&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxzZWFyY2h8M3x8ZGl5JTIweWFyZHxlbnwwfHwwfHx8MA%3D%3D',
		'title': 'Bathroom Storage Ladder',
		'author': '@bathroombliss'
	},
	{
		'img': 'https://images.unsplash.com/photo-1628702110466-aa2107a82d8e?w=800&auto=format&fit=crop&q=60&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxzZWFyY2h8NTJ8fGRpeSUyMGZ1cm5pdHVyZXxlbnwwfHwwfHx8MA%3D%3D',
		'title': 'Lighting Fixture Upgrade',
		'author': '@lightitup'
	}
];