import React from 'react';
import PageWrapper from '../components/PageWrapper';
import useAuth, { useAuthEffects } from '../components/Auth';
import { Avatar, Box, Button, Divider, FormControlLabel, FormGroup, Grid2, Switch, TextField, Typography, useTheme } from '@mui/material';
import { AccountBoxOutlined } from '@mui/icons-material';

const ProfileView = () => {

	useAuthEffects();

	const theme = useTheme();
	const {userProfile} = useAuth();

	return <PageWrapper>
		{userProfile && <Grid2 container spacing={8}>
			<Grid2 size={12}>
				<Typography variant='h5'>Profile</Typography>
				<Divider sx={{mt: 1}}/>
				<Box sx={{marginY:2}}>
					<Avatar sx={{ bgcolor: theme.palette.action.hover, color: theme.palette.text.primary, width: 196, height: 196, fontSize: 40, borderRadius: '5px' }} variant="square">
						<AccountBoxOutlined color='disabled' fontSize='large' />
					</Avatar>
				</Box>
				<Box sx={{marginY:2, maxWidth:540}}>
					<TextField label="Name" variant="filled" fullWidth defaultValue={userProfile.name} />
				</Box>
				<Box sx={{marginY:2, maxWidth:540}}>
					<TextField label="Region" variant="filled" fullWidth value='Global' disabled />
				</Box>
				<Box sx={{marginY:2, maxWidth:540}}>
					<TextField
						id="outlined-textarea"
						label="Bio"
						placeholder="A happy builder"
						multiline
						variant='filled'
						rows={4}
						fullWidth
						defaultValue={userProfile.bio}
					/>
				</Box>
				<Button variant='text' disabled>Publish changes</Button>
			</Grid2>
			<Grid2 size={12}>
				<Typography variant='h5'>Role preferences</Typography>
				<Divider sx={{mt: 1}}/>
				<Box sx={{marginY:2}}>
					<FormGroup>
						<FormControlLabel control={<Switch defaultChecked />} label="Maker" disabled />
						<FormControlLabel control={<Switch />} label="Innovator" disabled />
						<FormControlLabel control={<Switch />} label="Supplier" disabled />
					</FormGroup>
				</Box>
			</Grid2>
		</Grid2> }
	</PageWrapper>;
};

export default ProfileView;
