import React, { useEffect, useRef, useState } from 'react';
import { useTheme, Box, TextField, Button, Typography, List, ListItem, useMediaQuery, Chip } from '@mui/material';
import SendIcon from '@mui/icons-material/Send';
import useAuth from './Auth';
import AutoAwesomeIcon from '@mui/icons-material/AutoAwesome';

import dayjs from 'dayjs';
import { DATE_FORMAT } from '../constants';
import { useMessageSubmitMutation } from '../services/orderMessages';
import { Message } from '../types';

interface Props {
	orderId: number;
	messages: Message[];
	isOpen: boolean;
}

const ChatBox = ({ orderId, messages, isOpen }: Props) => {
	const {userProfile} = useAuth();
	const [currentMessage, setCurrentMessage] = useState('');
	const messagesEndRef = useRef<HTMLDivElement>(null);
	const [messageSubmitMutation, {isSuccess, data}] = useMessageSubmitMutation(); 

	const theme = useTheme();
	const isSmUp = useMediaQuery(theme.breakpoints.up('sm'));

	const handleSendMessage = () => {
		if (currentMessage.trim()) {
			const newMessage = {
				content: currentMessage.trim(),
				orderId,
			};
			messageSubmitMutation(newMessage);
		}
	};

	useEffect(() => {
		if (isSuccess) {
			setCurrentMessage('');
		}
	}, [isSuccess, data]);
    
	const handleSubmit = (e: React.FormEvent) => {
		e.preventDefault();
		handleSendMessage();
	};

	// Function to scroll to the bottom
	const scrollToBottom = () => {
		messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
	};

	// Scroll to bottom whenever messages update
	useEffect(() => {
		if (messages || isOpen) {
			scrollToBottom();
		}
	}, [messages, isOpen]);

	if (!userProfile) {
		return null;
	}
	
	return (
		<Box
			display='flex' flexDirection='column' height='100%'
		>
			<Box sx={{flex:1, overflow: 'auto', height:'100%'}}>
				<List>
					{messages.map((message, index) => {
						const shouldRenderTimeLabel = index === 0 || dayjs(messages[index].createdAt).subtract(1, 'minute').isAfter(messages[index-1].createdAt);
						const isOtherParty = message.companyId !== userProfile.companyId;
						return <ListItem key={index} sx={{paddingY: 0.25, width: '100%'}}>
							<Box display='flex' flexDirection='column' sx={{alignItems: isOtherParty ? 'end' : 'baseline', display:'flex', width: '100%'}}>
								{shouldRenderTimeLabel && <Typography width='100%' textAlign='center' variant='caption' color='grey' my={2}>{dayjs(message.createdAt).format(DATE_FORMAT)}</Typography>}
								<Chip icon={message.isAutomatic ? <AutoAwesomeIcon /> : undefined} label={message.content} sx={{
									height: 'auto',
									paddingY: 0.75,
									maxWidth: '70%',
									fontSize: 16,
									...(message.isAutomatic ? {background: 'transparent' } : {}),
									...(!message.isAutomatic && !isOtherParty ? {backgroundColor: theme.palette.primary.main, color: theme.palette.primary.contrastText} : {}),
									'& .MuiChip-label': {
										overflow: 'visible',
										textOverflow: 'initial',
										whiteSpace: 'pre-wrap',
									},
								}} />
							</Box>
						</ListItem>;}
					)}
				</List>
				<div ref={messagesEndRef} />
			</Box>
			<Box pt={1} pb={4}>
				<form onSubmit={handleSubmit}>
					<Box display='flex' alignItems='end'>
						<Box flex={isSmUp ? 1 : 1} sx={{maxWidth: 520}}>
							<TextField multiline maxRows={12} slotProps={{input: {sx: {borderRadius: 4}}}} variant='outlined' autoComplete='off' placeholder='Hi there , ' value={currentMessage} fullWidth onChange={(e) => setCurrentMessage(e.target.value)} />
						</Box>
						<Box flex={isSmUp ? 0 : 0}>
							<Button sx={{whiteSpace:'nowrap', p:2, ml: 1}} onClick={handleSendMessage} startIcon={<SendIcon />} disabled={false}>Send</Button>
						</Box>
					</Box>
				</form>
			</Box>
		</Box>
	);
};

export default ChatBox;
