import * as React from 'react';
import Box from '@mui/material/Box';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import Typography from '@mui/material/Typography';
import dayjs from 'dayjs';
import { DATE_FORMAT } from '../constants';
import { OrderStatus } from '../types';

const steps = [
	{ status: OrderStatus.Created, label: 'Order created' },
	{ status: OrderStatus.Prepared, label: 'Waiting for an offer...' },
	{ status: OrderStatus.Paid, label: 'Checkout' },
	{ status: OrderStatus.Shipped, label: 'Shipped' },
];

interface Props {
	orderStatus: OrderStatus;
	orderDate: string;
}

export default function StatusStepper(props: Props) {
	const activeStep = steps.findIndex((step) => step.status === props.orderStatus);

	return (
		<Box sx={{ width: '100%' }}>
			<Stepper activeStep={activeStep + 1} alternativeLabel>
				{steps.map((step, index) => {
					const stepProps: { completed?: boolean } = {completed: activeStep >= index};
					const labelProps: { optional?: React.ReactNode } = {};

					if (step.status === OrderStatus.Created) {
						labelProps.optional = (
							<Typography variant="caption" noWrap>
								{dayjs(props.orderDate).format(DATE_FORMAT)}
							</Typography>
						);
					}

					return (
						<Step
							key={step.label}
							{...stepProps}
						>
							<StepLabel {...labelProps}>{step.label}</StepLabel>
						</Step>
					);
				})}
			</Stepper>
		</Box>
	);
}
