import { Theme as MuiTheme } from '@mui/material/styles';

// TODO: make styled components expect theme as a property instead of using type casting
export type Theme = MuiTheme

export enum ThemeMode {
  Light = 'light',
  Dark = 'dark',
}

export interface PlanListItem {
  id: number;
  title: string;
  description: string;
  imgUrl: string;
  authorName: string;
  materialsCost?: string;
  timeCost?: string;
  ratingsSummary?: string;
  createdAt: string;
  publishedAt: string;
}

export interface SupplierListItem {
  id: number;
  img: string;
  name: string;
  description: string;
  ratingsSummary: string;
}

export interface PlanImageItem {
  imgUrl: string;
  title: string;
}

export interface InstructionVideoItem {
  youtubeId: string;
}

export interface ResourceListItem {
  id: number;
  imgUrl: string;
  name: string;
  description: string;
  quantity: string;
  uom: string;
  isTool: boolean;
}

interface CompanyDetails {
  name: string;
  bio: string;
  imgUrl: string;
}

export interface PlanDetails {
  id: number;
  title: string;
  description: string;
  ratingsSummary?: string;
  timeCost: string;
  materialsCost: string;
  // plan: PlanListItem;
  suppliers: SupplierListItem[];
  images: PlanImageItem[];
  videos: InstructionVideoItem[];
  materials: ResourceListItem[];
  tools: ResourceListItem[];
  author: CompanyDetails;
  resources: ResourceListItem[];
}

export interface OrderListItem {
  id: number;
  status: OrderStatus;
  itemsCount: number;
  supplierName: string;
  supplierImg: string;
  additionalInfo?: string;
  createdAt: string;
}

export interface Message {
  content: string;
  companyId: number;
  authorId: number;
  authorName: string;
  isAutomatic?: boolean; 
  createdAt: string;
}

export interface OrderDetails {
  main: OrderListItem,
  materials: ResourceListItem[];
  messages: Message[];
}

export enum OrderStatus {
  Created = 'created',
  Prepared = 'prepared',
  Paid = 'paid',
  Shipped = 'shipped',
}
