import {api} from './api';

export const plansApi = api.injectEndpoints({
	endpoints: (builder) => ({
		discoverLoad: builder.mutation({
			query: (args: void) => ({
				url: 'plans/discover',
				method: 'GET',
			}),
		}),
		planDetailsLoad: builder.mutation({
			query: (id: string) => ({
				url: `plans/${id}/details`,
				method: 'GET',
			}),
		}),
	}),
});

export const { useDiscoverLoadMutation, usePlanDetailsLoadMutation } = plansApi;
