import { List, Box, ListItem, ListItemAvatar, Avatar, alpha, ListItemText, Collapse, Divider, Checkbox, useTheme, useMediaQuery } from '@mui/material';
import React from 'react';
import { ResourceListItem } from '../types';
import CategoryIcon from '@mui/icons-material/Category';
import HourglassTopIcon from '@mui/icons-material/HourglassEmpty';

interface Props {
    items: ResourceListItem[];
}

const OrderViewResourceList = ({ items }: Props ) => {
	const theme = useTheme();
	const isSmUp = useMediaQuery(theme.breakpoints.up('sm'));
	return <List disablePadding>
		{items.map((resource, i) => (
			<Box key={i} sx={{background: theme.palette.background.paper, my: 1, borderRadius: 2}} boxShadow={1}>
				<ListItem sx={{display:'flex', p: 0}}>
					<ListItemAvatar>
						<Avatar src={resource?.imgUrl} variant='square' sx={{ width: 96, height: 96, mr: 2, borderTopLeftRadius: 8, borderBottomLeftRadius: 0, background: alpha(theme.palette.getContrastText(theme.palette.background.default), 0.12), color: 'divider' }}>
							<CategoryIcon fontSize='large'  />
						</Avatar>
					</ListItemAvatar>
					<ListItemText
						primary={resource.name}
						secondary={resource.description}
						sx={{
							p: 1,
							flex: 'auto',
							'& > p': {whiteSpace: 'nowrap', textOverflow: 'ellipsis', overflow: 'hidden'},
						}}
					/>
					<ListItemText
						sx={{
							flex: 'auto',
							minWidth: 'auto',
							mr: 4,
							textAlign: 'right', 
							'& > span': {display:'inline-block', whiteSpace: 'nowrap', mr: 0.5},
							'& > p': {display:'inline-block'},
						}}
						primary={resource.quantity}
						secondary={resource.uom}
					/>
				</ListItem>
				<Collapse in timeout="auto" unmountOnExit sx={{}}>
					<Divider />
					<List component="div" disablePadding>
						<ListItem sx={{ py: 0.5, px: 1, borderRadius: 2, background: theme.palette.background.paper, '&:first-of-type': {borderTopLeftRadius: 0, borderTopRightRadius: 0} }}>
							<Checkbox
								disabled
								icon={<HourglassTopIcon />}
								checked={false}
							/>
							<ListItemText primary='Offer requested'/>
						</ListItem>
					</List>
				</Collapse>
			</Box>
		))}
	</List>;
};

export default OrderViewResourceList;
