import {createSelector, createSlice, PayloadAction} from '@reduxjs/toolkit';
import {RootState} from '../store';
import { plansApi } from '../services/plans';
import { PlanListItem, PlanDetails } from '../types';

interface ContentState {
	planListItems: PlanListItem[];
	planDetails: PlanDetails | null;
}

const initialState: ContentState = {
	planListItems: [],
	planDetails: null,
};

export const planSlice = createSlice({
	name: 'plans',
	initialState,
	reducers: {
		planListItemsSetState(state: ContentState, action) {
			state.planListItems = action.payload;
			return state;
		},
		planDetailsSetState(state: ContentState, action) {
			state.planDetails = action.payload;
			return state;
		},
	},
	extraReducers: (builder) => {
		builder
			.addMatcher(plansApi.endpoints.discoverLoad.matchFulfilled, (state: ContentState, action: PayloadAction<PlanListItem[]>) => {
				planSlice.caseReducers.planListItemsSetState(state,	planSlice.actions.planListItemsSetState(action.payload));
			})
			.addMatcher(plansApi.endpoints.planDetailsLoad.matchFulfilled, (state: ContentState, action: PayloadAction<PlanDetails>) => {
				planSlice.caseReducers.planDetailsSetState(state,	planSlice.actions.planDetailsSetState(action.payload));
			});
	},
});

export const {
	planListItemsSetState,
	planDetailsSetState,
} = planSlice.actions;

export const selectPlansState = (state: RootState) => state.plans;

export const selectPlanListItems = createSelector([selectPlansState], (state) => state.planListItems);

export const selectPlanDetails = createSelector([selectPlansState], (state) => state.planDetails ?? null);

export const selectPlanAuthor = createSelector([selectPlansState], (state) => state.planDetails?.author ?? null);

export const selectPlanSuppliers = createSelector([selectPlansState], (state) => state.planDetails?.suppliers ?? []);

export const selectPlanImages = createSelector([selectPlansState], (state) => state.planDetails?.images ?? []);

export const selectPlanVideos = createSelector([selectPlansState], (state) => state.planDetails?.videos ?? []);

const selectPlanResources = createSelector([selectPlansState], (state) => state.planDetails?.resources ?? []);

export const selectPlanResourcesMaterials = createSelector([selectPlanResources], (resources) => resources.filter(({isTool}) => !isTool));

export const selectPlanResourcesTools = createSelector([selectPlanResources], (resources) => resources.filter(({isTool}) => isTool));

export default planSlice.reducer;
