import React from 'react';
import styled from '@emotion/styled';
import CssBaseline from '@mui/material/CssBaseline';
import {
	BrowserRouter as Router,
	Routes,
	Route,
} from 'react-router-dom';
import {Theme} from './types';
import {Helmet} from 'react-helmet';
import config from './common/config';
import {ThemeProvider} from './ThemeProvider';
import SettingsView from './containers/ProfileView';
import DiscoverView2 from './containers/DiscoverView';
import CategoriesView from './containers/CategoriesView';
import SavedView from './containers/SavedView';
import PlanView from './containers/PlanView';
import OrdersView from './containers/OrdersView';
import OrderDetailView from './containers/OrderDetailView';

const Page = styled.div`
  background-color: ${({ theme }) => (theme as Theme).palette.background.default};
  color: ${({ theme }) => (theme as Theme).palette.text.primary};
`;

function App() {
	return (
		<ThemeProvider>
			<CssBaseline />
			<Page>
				<Helmet>
					<meta charSet="utf-8" />
					<title>{config.appName}</title>
					<link rel="canonical" href="http://mysite.com/example" />
				</Helmet>
				<Router>
					<Routes>
						<Route path="/profile" element={<SettingsView />} />
						<Route path="/" element={<DiscoverView2 />} />
						<Route path="/categories" element={<CategoriesView />} />
						<Route path="/saved" element={<SavedView />} />
						<Route path="/plans/:id" element={<PlanView />} />
						<Route path="/orders" element={<OrdersView />} />
						<Route path="/orders/:id" element={<OrderDetailView />} />
					</Routes>
				</Router>
			</Page>
		</ThemeProvider>
	);
}

export default App;
