import React, { useEffect } from 'react';
import PageWrapper from '../components/PageWrapper';
import { useAuthEffects } from '../components/Auth';
import { useTheme, Avatar, Box, List, ListItem, ListItemAvatar, ListItemButton, ListItemText, Typography, MobileStepper, useMediaQuery } from '@mui/material';
import FolderIcon from '@mui/icons-material/Folder';
import { useNavigate } from 'react-router-dom';
import { useOrderListLoadMutation } from '../services/orders';
import { useAppSelector } from '../hooks';
import dayjs from 'dayjs';
import { selectOrdersListItems } from './orderSlice';
import { screenHeightMinusHeaderStyle } from '../common/util';

const OrdersView = () => {
	useAuthEffects();

	const theme = useTheme();
	const navigate = useNavigate();
	const [orderListLoadMutation] = useOrderListLoadMutation();
	const orders = useAppSelector(selectOrdersListItems);

	useEffect(() => {
		orderListLoadMutation();
	}, []);

	const isSmUp = useMediaQuery(theme.breakpoints.up('sm'));

	return <PageWrapper disableFooter={!orders.length}>
		{!orders.length ? <Box sx={{height: screenHeightMinusHeaderStyle(isSmUp)}} display='flex' alignItems='center' justifyContent='center'>
			<span>
				No orders yet !
			</span>
		</Box> :
			<Box>
				<Typography variant='body2' sx={{mt:2}}>My orders</Typography>
				<List>
					{orders.map((order, i) => (
						<ListItem key={i}>
							<ListItemButton onClick={() => navigate(`/orders/${order.id}`)}>
								<ListItemAvatar>
									<Avatar src={order.supplierImg} variant='square' sx={{ width: 96, height: 96, mr: 2, borderRadius: 2 }}>
										<FolderIcon />
									</Avatar>
								</ListItemAvatar>
								<ListItemText
									primary={<span>{order.itemsCount} items from {order.supplierName}</span>}
									secondary={dayjs(order.createdAt).format('MMMM D, HH:mm')}
								/>
								<Box justifyItems='center'>
									<Box>
										{order.status}
									</Box>
									<MobileStepper
										sx={{bgcolor: 'transparent'}}
										variant="dots"
										steps={4}
										activeStep={0}
										position="static"
										backButton={undefined}
										nextButton={undefined}
									/>
								</Box>
							</ListItemButton>
						</ListItem>
					))}
				</List> 
			</Box>}
	</PageWrapper>;
};

export default OrdersView;
