import {api} from './api';

export const ordersApi = api.injectEndpoints({
	endpoints: (builder) => ({
		orderSubmit: builder.mutation({
			query: (data: object) => ({
				url: 'orders',
				body: data,
				method: 'POST',
			}),
		}),
		orderListLoad: builder.mutation({
			query: (args: void) => ({
				url: 'orders/list',
				method: 'GET',
			}),
		}),
		orderDetailsLoad: builder.mutation({
			query: (orderId: string) => ({
				url: `orders/${orderId}/details`,
				method: 'GET',
			}),
		}),
		orderDelete: builder.mutation({
			query: (orderId: string) => ({
				url: `orders/${orderId}`,
				method: 'DELETE',
			}),
		}),
	}),
});

export const {useOrderSubmitMutation, useOrderDeleteMutation, useOrderListLoadMutation, useOrderDetailsLoadMutation} = ordersApi;
